<template>
  <v-form
    ref="form"
    class="centered px-6"
    v-model="valid"
    lazy-validation
  >
    <v-text-field
      v-model="email"
      label="Email"
      required
    ></v-text-field>
    <!-- :counter="10" -->
    <!-- :rules="nameRules" -->

    <v-text-field
    v-model="password"
      label="Password"
      required
    ></v-text-field>
    <!-- :rules="emailRules" -->
    t@t.com
    chilling




    <v-btn
      :disabled="!valid"
      color="success"
      class="mr-4"
      @click="validate"
    >
      Login
    </v-btn>
    <v-btn
      color="fail"
      class="mr-4"
      @click="logout"
    >
      Logout
    </v-btn>




  </v-form>
</template>

<script>
import firebase from "firebase";
  export default {
    name:'loginForm',
    data: () => ({
      valid: true,
      name: '',
      nameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length <= 10) || 'Name must be less than 10 characters',
      ],
      email: '',
      emailRules: [
        v => !!v || 'Password is required'

      ],
      password: '',
      select: null,
      items: [
        'Item 1',
        'Item 2',
        'Item 3',
        'Item 4',
      ],
      checkbox: false,
    }),

    methods: {
      validate () {

        this.$refs.form.validate()
        this.login()
      },
      reset () {
        this.$refs.form.reset()
      },
      login (){
        console.log(this.email,this.password);
        firebase.auth().signInWithEmailAndPassword(this.email, this.password).then(

          user => {
            console.log(user);
          },
          err => {
            alert(err);
          }
        )

        // log the user in
      },
      logout (){
        console.log("inside the logout method");
        firebase.auth().signOut().then(() => {
        // Sign-out successful.
        console.log("logged out");
      }).catch((error) => {
        // An error happened.
        console.log("errorrr");
      });
      }

    },
  }
</script>
